header {
  position: relative;
  height: 50px; }

nav {
  position: fixed;
  width: 100%;
  height: 50px;
  background-color: #212121;
  z-index: 5; }

.hamburger {
    display: none; }

.menu {
  display: flex;
  justify-content: space-between;
  padding: 13px 70px;
  margin: 0;
  list-style-type: none;
  &_link {
    color: #ffffff;
    font-size: 15px;
    font-weight: 300;
    &:hover {
      text-decoration: none;
      color: $color-stack; } }
  &_item {
    border-right: 2px dotted black;
    padding-right: 28px;
    &:last-child {
      border-right: none; } } }

.subheader {
  position: absolute;
  top: 50px;
  width: 100%;
  height: 84px;
  background-color: rgba(0, 0, 0, 0.6);
  &_logo {
    display: block;
    max-width: 100%;
    margin-top: 16px; }
  &_official {
    margin-top: 6px;
    opacity: 0.5;
    color: #ffffff;
    font-size: 13px;
    font-weight: 400; }
  &_call {
    color: #ffffff;
    font-size: 12px;
    font-weight: 300;
    margin-top: 19px;
    text-align: right; }
  &_phone {
    display: block;
    margin-top: 8px;
    color: #ffffff;
    font-size: 22px;
    font-weight: 400;
    text-transform: uppercase;
    text-align: right;
    line-height: 12px;
    &:hover {
      text-decoration: none;
      color: #fff; } }
  &_btn {
    margin-top: 19px;
    width: 172px;
    height: 46px;
    box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
    border: none;
    border-radius: 4px;
    background-image: linear-gradient(to top, $color-stack 0%, #4ce2ff 100%);
    text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
    color: #ffffff;
    font-size: 14px;
    font-weight: 400;
    text-transform: uppercase;
    @include btn_hover; } }

.promo {
    min-height: 800px;
    background: url(../img/uber_bg.png) center center/cover no-repeat;
    padding: 150px 0 135px 0;
    &_header {
        text-align: center;
        color: #fff;
        font-weight: 900px;
        font-size: 60px; }
    &_subheader {
        text-align: center;
        color: #fff;
        font-weight: 700px;
        font-size: 36px; }
    &_descr {
        text-align: center;
        font-family: 'Roboto';
        font-style: normal;
        font-weight: 400;
        font-size: 18px;
        color: #fff; }
    &_btn {
        display: block;
        margin: 115px auto;
        background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
        box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.12);
        border-radius: 4px;
        width: 259px;
        height: 63px;
        font-weight: 400;
        font-size: 18px;
        line-height: 63px;
        text-transform: uppercase;
        color: #fff;
        @include btn_hover; } }

.label {
    position: relative;
    margin: 0 auto;
    height: 24px;
    width: 112px;
    padding: 5px 12px;
    background-color: #000000;
    color: #ffffff;
    font-size: 11px;
    font-weight: 300;
    text-transform: uppercase;
    text-align: center;
    z-index: 2;
    &_white {
        background-color: #fff;
        color: #000;
        position: relative;
        margin: 0 auto;
        height: 24px;
        width: 112px;
        padding: 5px 12px;
        font-size: 11px;
        font-weight: 300;
        text-transform: uppercase;
        text-align: center;
        z-index: 2;
        &_min {
            background-color: #fff;
            color: #000;
            position: relative;
            margin: 0 auto;
            padding: 5px 12px;
            font-size: 11px;
            font-weight: 300;
            text-transform: uppercase;
            text-align: center;
            z-index: 2;
            width: 79px;
            height: 27px; } } }


.title {
    margin-top: 32px;
    font-style: normal;
    font-weight: 700;
    font-size: 38px;
    line-height: 48px;
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 16px;
    &_white {
        margin-top: 32px;
        font-style: normal;
        font-weight: 700;
        font-size: 38px;
        line-height: 48px;
        text-align: center;
        text-transform: uppercase;
        margin-bottom: 16px;
        color: #fff; } }

.subtitle {
    font-weight: 400;
    font-size: 17px;
    line-height: 21px;
    text-align: center;
    margin-bottom: 30px;
    &_white {
        color: #fff;
        font-weight: 400;
        font-size: 17px;
        line-height: 21px;
        text-align: center;
        margin-bottom: 30px; } }


.reasons {
    padding: 71px 0 65px 0;
    &_block {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 35px; }
    &_round {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-right: 26px;
        width: 113px;
        height: 113px;
        background-color: #1eacc7;
        border-radius: 100%; }
    &_descr {
         width: 343px; }
    &_subtitle {
        font-weight: 600;
        font-size: 17px;
        line-height: 21px;
        color: $color-stack; }
    &_text {
        font-weight: 300;
        font-size: 17px;
        line-height: 21px;
        color: #000; } }
.mobile {
    background: url(../img/Подложка.png) center center/cover no-repeat;
    min-height: 865px;
    &_item {
        position: relative;
        width: 320px;
        height: 280px;
        margin: 0 auto;
        margin-bottom: 19px;
        padding-top: 92px;
        &_1 {
            background: url('../img/Карточка1.png') center (center / cover) no-repeat; }
        &_2 {
            background: url('../img/Карточка2.png') center (center / cover) no-repeat; }
        &_3 {
            background: url('../img/Карточка3.png') center (center / cover) no-repeat; }
        &_4 {
            background: url('../img/Карточка4.png') center (center / cover) no-repeat; }
        &_5 {
            background: url('../img/Карточка5.png') center (center / cover) no-repeat; }
        &_6 {
            background: url('../img/Карточка6.png') center (center / cover) no-repeat; }
        &_subtitle {
            color: #ffffff;
            font-size: 24px;
            font-weight: 400;
            line-height: 31px;
            text-transform: uppercase;
            text-align: center; } }
    &_plus {
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        bottom: 35px;
        left: 50%;
        transform: translateX(-50%);
        width: 26px;
        height: 26px;
        background-color: #000000;
        margin: 0 auto;
        border-radius: 100%;
        cursor: pointer; } }

.choice {
    min-height: 898px;
    background: url("../img/Group.png") center center/cover no-repeat;
    padding: 64px 0 138px 0;
    &_img {
        display: block;
        margin: 54px auto 0 auto; }
    &_descr {
        font-weight: 300;
        font-size: 17px;
        line-height: 24px;
        text-align: center; } }

.require {
    padding: 68px 0 84px 0;
    &_block {
        margin: 33px 0;
        &_nmb {
            margin-bottom: 0px; } }
    &_title {
        color: #1ca8c3;
        font-size: 22px;
        font-weight: 400; }
    &_list {
        list-style-type: none;
        padding-left: 50px;
        margin-top: 22px;
        li {
            position: relative;
            font-size: 17px;
            font-weight: 300;
            line-height: 24px;
            margin-bottom: 10px;
            &:before {
                content: '';
                display: block;
                position: absolute;
                left: -32px;
                top: 3px;
                width: 21px;
                height: 20px;
                background: url('../img/Rectangle-9.png') center (center / cover) no-repeat; } } }
    &_descr {
        margin-top: 25px;
        font-size: 17px;
        font-weight: 300;
        line-height: 24px;
        color: #1ca8c3; }
    .warning {
        .require_title {
            color: red; }
        .require_list {
            li {
                &:before {
                    background: url('../img/red.png') center (center / cover) no-repeat; } } } } }

.world {
    position: relative;
    min-height: 717px;
    padding: 49px 0;
    background: url('../img/world.png') center (center / cover) no-repeat;
    &_link {
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        bottom: 49px;
        text-align: center;
        font-size: 15px;
        font-weight: 400;
        line-height: 68px;
        text-transform: uppercase;
        color: #1fbad6;
        &:hover {
            text-decoration: none;
            color: #fff; } } }
footer {
    padding: 14px 23px 39px 23px;
    a {
        &:hover {
            text-decoration: none; } } }
.footer {
    &_divider {
        width: 299px;
        height: 4px;
        background-color: #1fbad6;
        margin: 0 auto; }
    &_wrapper {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        padding-top: 32px; }
    &_wrapper > div {
        width: 33%;
        &:nth-child(3) {
            display: flex;
            justify-content: flex-end; } }
    &_social {
        width: 160px;
        display: flex;
        justify-content: space-between;
        &_item {
            display: flex;
            justify-content: center;
            align-items: center;
            margin: 0 4px;
            width: 32px;
            height: 32px;
            background-color: #070716;
            border-radius: 100%;
            i {
                color: #fff; } } }
    &_links {
        width: 390px;
        margin: 0 auto;
        &_main {
            display: flex;
            justify-content: space-between;
            padding: 0 10px;
            a {
                color: #070716;
                font-size: 15px;
                font-weight: 300;
                text-transform: uppercase; } }
        &_sub {
            display: flex;
            justify-content: space-between;
            margin-top: 10px;
            a {
                color: #999999;
                font-size: 11px;
                font-weight: 300;
                text-transform: uppercase; } }
        &_lang {
            display: block;
            margin: 0 auto;
            color: #1fbad6;
            font-size: 11px;
            font-weight: 400;
            margin-top: 19px;
            text-transform: uppercase;
            text-align: center; } } }
.xs-visible {
	display: none; }
